.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 30px;
  font-family: 'Courier New', Courier, monospace;
  flex-shrink: 0;
}

.bg-blur {
  position: fixed;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  background-color: rgba(0, 255, 255, 0);
  backdrop-filter: blur(7px);
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: fit-content;
  width: 500px;
  padding: 15px;
  background-color: white;
  border-style: solid;
  border-radius: 10px;
  border-width: 0px;
  border-color: black;
  background-color: white;
  box-shadow: 0px 2px 10px rgb(95, 95, 95);
  z-index: 10;
  font-size: 16px;
}

.InputModal {
  width: fit-content;
}

form button,
.modal button {
  font-size: 16px;
  width: 140px;
}

label{
  margin-right:5px;
}

.interval-spacer{
  width: 140px;
}

.form-button-container {
  display: flex;
  flex-direction: column;
}

form {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 2px;
}

input {
  height: 20px;
  width: 200px;
  margin-right: 4px;
  font-family: 'Courier New', Courier, monospace;
}

textarea {
  height: 100px;
  width: 345px;
  margin-right: 4px;
  border-style: solid;
  border-radius: 4px;
}

.centerer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.greyed-out {
  color: grey;
  background-color: white;
}

:root {
  --instruction-dims: 50px;
}



.list {
  display: flex;
  flex-direction: row;
}

.InstructionList {
  width: 95vw;
  display: grid;
  gap: 1px;
  grid-template-columns: repeat(auto-fit, 54px);
  grid-auto-rows: auto;
  padding: .5vw;
  border-style: solid;
  border-radius: 8px;
  background-color: rgb(224, 224, 224);
}

.InstructionPointer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: var(--instruction-dims);
  height: 20px;
  font-size: 20px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0);
  border-radius: 4px;
  margin: 2px;
  flex-shrink: 0;
}

.instruction-pointer {
  color: red;
}

.instruction-pointer-img {
  height: 20px;
}

.Instruction {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--instruction-dims);
  aspect-ratio: 1 / 1;
  font-size: 40px;
  background-color: white;
  border-style: solid;
  border-radius: 4px;
  margin: 2px;
  flex-shrink: 0;
}

.ran {
  color: grey;
}

.ran.start {
  background-color: black;
}

.display-end.start,
.display-end.end {
  background-color: black;
  color: grey;
}

.display-end.start.instruction-pointer,
.display-end.end.instruction-pointer {
  background-color: black;
  color: rgb(255, 0, 0);
}



.spacer {
  position: relative;
  height: 40px;
}

.cells-container {
  width: 100vw;
}

.Cell {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--instruction-dims);
  aspect-ratio: 1 / 1;
  font-size: 20px;
  border-style: solid;
  border-radius: 4px;
  margin: 2px;
  flex-shrink: 0;
  overflow: hidden;
}

.negative {
  background-color: red;
}

.cell-pointer {
  color: green;
  height: var(--cell-dims);
}

.cell-pointer-img {
  height: 30px;
}

.Pointer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: var(--instruction-dims);
  height: 40px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0);
  border-radius: 4px;
  margin: 2px;
  flex-shrink: 0;
}

h3 {
  position: relative;
  font-weight: normal;
  margin: 0px
}

.OutputList {
  position: relative;
  height: 54px;
}

.Output {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  overflow: hidden;
}

.output-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  aspect-ratio: 1 / 1;
  font-size: 20px;
  border-style: solid;
  border-color: rgb(0, 0, 0);
  border-radius: 9px;
  margin: 2px;
}

.output-string {
  position: relative;
  height: 33px;

}

.button-container {
  position: relative;
  display: flex;
  flex-direction: row;
  position: fixed;
  right: 0px;
  bottom: 0px;

}

button {
  font-family: 'Courier New', Courier, monospace;
  font-size: 40px;
}